import React, { useContext } from 'react';
import { Card, Form, Input, Row, Col, Button, Select, Switch } from 'antd';
import { QosOption } from './index'

const Publisher = ({ publish }) => {
  const [form] = Form.useForm();
  const qosOptions = useContext(QosOption);

  // topic, QoS for publishing message
  const record = {
    topic: 'switch',
    qos: 0,
    payload: `{state:"on"}`
  };

  const onFinish = (values) => {
    publish(values)
  };

  const PublishForm = (
    <Form
      layout="vertical"
      name="basic"
      form={form}
      initialValues={record}
      onFinish={onFinish}
    >
      <Row gutter={20}>
        <Col span={12}>
          <Form.Item
            label="Topic"
            name="topic"
          >
            <Input />
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item
            label="QoS"
            name="qos"
          >
            <Select options={qosOptions} />
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item
            label="Payload"
            name="payload"
          >
            <Input.TextArea />
          </Form.Item>
        </Col>
        <Col span={8} offset={16} style={{ textAlign: 'right' }}>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              Publish
            </Button>
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={8}>
        <Col>
          <Button
            type="danger"
            onClick={() => {
              publish({
                topic: 'switch',
                qos: 0,
                payload: `{state:"off"}`
              })
            }}>关闭</Button>
        </Col>
        <Col>
          <Button
            type="primary"
            onClick={() => {
              publish({
                topic: 'switch',
                qos: 0,
                payload: `{state:"on"}`
              })
            }}>开启</Button>
        </Col>
      </Row>
    </Form>
  )

  return (
    <Card
      title="Publisher"
    >
      {PublishForm}
    </Card>
  );
}

export default Publisher;
